@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  font-family:'Courier New', Courier, monospace;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url('./components/bg_enhanced.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
}

h1 {
  color: #000000;
  margin-bottom: 0.5rem;
  font-size: 50px;
  font-weight: bolder;
}


.TodoWrapper {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  backdrop-filter: blur(10px);
  margin-top: 5rem;
  padding: 2rem;
  border-radius: 10px;
}

.TodoForm {
  width: 100%;
  margin-bottom: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-input {
  outline: none;  
  background: none;
  border: 1px solid black;
  padding: 0.5rem 1rem;
  width: 350px;
  color: #161D6F;
  font-size: 22px;
  font-weight: bolder;
  border-radius: 10px;
}

.todo-input::placeholder {
  color: #227B94;
}

.todo-btn {
  background: transparent;
  display: flex;
  border: none;
  cursor: pointer;
  font-size: 40px;
  flex-direction: row;
  padding: 1px;
}


.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: black;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 5px;
  margin-bottom: 1rem;
  font-size: 23px;
}

.delete-icon {
  margin-left: 0.75rem;
}

.clip {
  font-size: 36px;
  padding: 3px;
  padding-right: 10px;
}

.incompleted {
  color: #fff;
  cursor: pointer;
}

.completed {
  color: #c5aeff;
  text-decoration: line-through;
  cursor: pointer;
}

.edit-icon,
.delete-icon {
  cursor: pointer;
}